.carousel{
    width: 80%;
    height: 200px;
    background-color: black;
    margin-bottom: 20px;
}

.corousel-inner{
    height: 100%;
    width: 100%;
    backface-visibility: visible;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
}